'use client';

import Button from '@/components/Button/Button';
import Modal from '@/components/Modal';
import { useState } from 'react';

const LoginPage = () => {
  const [mode, setMode] = useState<'signin' | 'signup'>('signup');

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const toggleMode = () => {
    setMode(mode === 'signin' ? 'signup' : 'signin');
  };

  const title = mode === 'signin' ? 'Welcome back' : 'Create an account';
  const subtitle =
    mode === 'signin'
      ? ' Log in to continue tracking your mood and sleep.'
      : 'Join to track your daily mood and sleep with ease.';

  return (
    <Modal displayOverlay={false} bgGradient={false}>
      <form className="flex flex-col gap-400">
        <div className="flex flex-col gap-100">
          <h1 className="preset-3 font-bold text-neutral-900">{title}</h1>
          <p className="preset-6 text-neutral-600">{subtitle}</p>
        </div>
        <div className="flex flex-col gap-250">
          <div className="flex flex-col gap-100">
            <label htmlFor="email" className="preset-6 text-neutral-900">
              Email
            </label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="name@example.com"
              className="preset-6 rounded-10 bg-neutral-0 border border-neutral-300 px-200 py-100 py-150 text-neutral-900"
            />
          </div>
          <div className="flex flex-col gap-100">
            <label htmlFor="password" className="preset-6 text-neutral-900">
              Password
            </label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="preset-6 rounded-10 bg-neutral-0 border border-neutral-300 px-200 py-100 py-150 text-neutral-900"
            />
          </div>
        </div>
        <Button
          className="w-full"
          label={mode === 'signin' ? 'Login' : 'Sign up'}
          onClick={() => {}}
        />
      </form>
      <p className="preset-6 text-center text-neutral-600">
        {mode === 'signin' ? "Haven't got an account?" : 'Already have an account?'}
        <button className="preset-6 ml-2 text-blue-600" onClick={toggleMode}>
          {mode === 'signin' ? 'Sign up' : 'Login'}
        </button>
      </p>
    </Modal>
  );
};

export default LoginPage;
